.classcentral-style #editor {
  text-indent: -9999px;
  border: 1px solid #E2E2E2;
}
.classcentral-style #editor.ql-container {
  text-indent: 0px;
}
.classcentral-style .bg-slate {
  background-color: #5B6890;
}
.classcentral-style .bg-slate-light {
  background-color: #C9CEDB;
}
.classcentral-style .border--navy {
  border-color: #25366A !important;
}
.classcentral-style .border--slate-light {
  border-color: #C9CEDB !important;
}
.classcentral-style .text--navy,
.classcentral-style a.text--navy {
  color: #25366A;
  text-decoration: none;
}
.classcentral-style .text--slate,
.classcentral-style a.text--slate {
  color: #5B6890;
  text-decoration: none;
}
.classcentral-style .text--green,
.classcentral-style a.text--green {
  color: #3AB16C;
}
.classcentral-style select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}
.classcentral-style .btn-block.active {
  background-color: #25366A;
}



